"use client";

import { useEffect, useState } from "react";
import Header from "@/components/Layout/Header/Header";
import Banner from "@/components/banner";
import { InfoProvider } from "@/context/InfoContext";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Splash from "@/components/Splash/Splash"; // splash component

export default function ClientLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [showSplash, setShowSplash] = useState(true);

  useEffect(() => {
    const hasVisited = localStorage.getItem("hasVisited");

    if (hasVisited) {
      setShowSplash(false);
    } else {
      setShowSplash(true);
      localStorage.setItem("hasVisited", "true");

      // auto ẩn splash sau 2s
      setTimeout(() => setShowSplash(false), 2000);
    }
  }, []);

  if (showSplash) return <Splash />;

  return (
    <InfoProvider>
      <ToastContainer position="top-right" autoClose={3000} />
      <Header />
      <Banner />
      {children}
      <footer className="flex items-center justify-center h-[46px] bg-[#f4f4f4]">
        <p className="text-[15px] font-normal">Copyright © 2024 My MDD Diary</p>
      </footer>
    </InfoProvider>
  );
}
